.alerta {
    padding: 0.5rem;
    text-align: center;
    color: $white;
    font-weight: bold;
    text-transform: uppercase;
    margin: 1rem 0;
    &.error {
        background-color: $red;
    }

    &.exito {
        background-color: $green;
    }
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0px;

    thead {
        background-color: $green;

        th {
            color: $white;
            padding: 2rem;
        }
        
    }

    .imagen-tabla {
        width: 10rem;
    }
}

.imagen-small {
    width: 20rem;
}

.admButtons {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}