.dark-mode {
  background-color: darken($darkGrey, 10);
  p {
    color: $white;
  }
  a,
  h1,
  h2,
  h3,
  h4 {
    color: $grey;
  }
  .anuncio,
  .resumen-propiedad {
    background-color: $darkGrey;
    border: 1px solid $darkGrey;
    .precio {
      color: $green;
    }
  }
  .icono-d {
    filter: invert(100%);
  }
  .texto-nosotros blockquote {
    color: $white;
  }
  .formulario {
    label,
    legend,
    p {
      color: $grey;
    }
    input:not([type="submit"]),
    textarea,
    select {
      background-color: $darkGrey;
      color: $white;
      border: 1px solid $darkGrey;
    }
  }

  td {
    color: $white;
  }
}
