@include tablet {
    .seccion-inferior {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog {
    margin-bottom: 3rem;
    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        gap: 2.5rem;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
}

.informacion-meta {
    span {
        color: $yellow;
    }
}

.texto-entrada {
    a {
        color: black;
        margin-top: 2rem;
        display: block;
        @include tablet {
            margin-top: 0;
        }
    }

    h4 {
        margin: 0;
        line-height: 1.4rem;

        &::after {
            content: '';
            display: block;
            margin-top: 2rem;
            width: 15rem;
            height: .5rem;
            background-color: $green;
        }
    }


}