@include tablet {
    .contenedor-anuncios {
        @include grid (3, 2rem);
    }
}

.anuncio {
    background-color: lighten($grey, 5);
    border: 1px, solid lighten($grey, 10);
    margin-bottom: 2rem;

    .contenido-anuncio {
        padding: 2rem;
    }

    h3, p {
        margin: 0;
    }

}

.precio {
    color: $green;
    font-weight: bold;
    font-size: 3rem;

}

.iconos-caracteristicas {
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;
    
    li {
        flex: 1;
        display: flex;

        img {
            flex: 0 0 5px;
            margin-right: 1rem;
        }
        
        p{
            font-weight: $black;
        }
    }
}